import _ from 'lodash';
import './App.css';

import logoWhite from './logo/ucid-logo-white.png';
import Navbar from './components/Navbar/Navbar';
import { CircularProgress, Typography } from '@mui/material';
import DisplayRemidiation from './components/DisplayRemediation/DisplayRemediation';
import { useCallback, useState } from 'react';
import RefreshButton from './components/RefreshButton/RefreshButton';
import { Box } from '@mui/system';

function App() {
  const [tableData, setTableData] = useState([]);
  const [showLoader, setShowLoader] = useState(false);

  const username = 'regu1'
  const password = 'passuser'

  // const baseURL = 'http://65.2.132.238:4000/api/regulator/';
  const baseURL = 'http://18.207.108.70:4000/api/regulator/';

  const handleClickReferesh = useCallback(async () => {
    setShowLoader(true);
    const getRequestOptions = {
      method: 'GET',
      headers: {
        'Authorization': 'Basic ' + btoa(`${username}:${password}`),
      },
      mode: 'cors',
      cache: 'default'
    };

    const neededDeveicesRes = await fetch(`${baseURL}/event/RemediationNeeded`, getRequestOptions);
    const neededDevicesData = await neededDeveicesRes.json();
    let devicesToBeDisplayed = [];
    let devicesList = [];
    if (neededDevicesData.success) {
      const uniqueDevices = [... new Map(neededDevicesData.data.map(item =>
        [item['eventData.deviceID'], item])).values()];
      devicesToBeDisplayed = uniqueDevices.slice(0, 10);
    }
    if (!_.isEmpty(devicesToBeDisplayed)) {
      devicesList = devicesToBeDisplayed.map((ele) => {
        return ele.eventData.deviceID
      });
    }

    const remediationTableData = await Promise.all(
      devicesList.map(async (device) => {
        const tableRowRes = await fetch(`${baseURL}/device/search?ucid=${device}`, getRequestOptions);
        const tableRowData = await tableRowRes.json();
        return tableRowData;
      }));

    setTableData(remediationTableData);
    setShowLoader(false);

  }, []);

  return (
    <div className="App">
      <Navbar logo={logoWhite} />
      <Typography
        variant="h3"
        component="h3"
        mt={2}
      >
        List of devices which needs remediation
      </Typography>
      {
        showLoader &&
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <CircularProgress />
        </Box>
      }
      {
        !showLoader &&
        <DisplayRemidiation
          tableData={tableData}
        />
      }
      <RefreshButton
        handleClickReferesh={handleClickReferesh}
      />
    </div>
  );
}

export default App;
