import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { Tooltip } from '@mui/material';


export default function RefreshButton({ handleClickReferesh }) {
    return (
        <Stack
            direction={{ xs: 'column', sm: 'row' }}
            spacing={{ xs: 2, sm: 8, md: 12, lg: 24 }}
            m={{ xs: 2, sm: 4, md: 6, lg: 6 }}
            // spacing={24}
            alignItems="center"
            justifyContent="center"
        >

            <Tooltip
                title="Refresh table data"
                arrow
                placement='bottom-end'
            >
                <Button
                    variant="outlined"
                    onClick={handleClickReferesh}
                    color="primary"
                    sx={{
                        padding: 2,
                        fontWeight: 'normal',
                        fontSize: 18
                    }}
                >
                    Refresh
                </Button>
            </Tooltip>
        </Stack >
    )
};
